import React, { useState, useRef } from "react";
import { StyleSheet, Text, View } from "react-native";
import { Fonts } from "../utils/fonts";
import { RowTari } from "../components/rowTariffe";
import { ButtonAnni } from "../components/buttonAnni";
import { Form, Col, Container, Row, Button } from "react-bootstrap";
export const HomeScreen = () => {
  const [selected, setSelected] = useState(
    "2021,2021d,2020,2020d,2019,2019d,2018,2018d,2017"
  );
  const [mq, setMq] = useState(0);
  const [comp, setComp] = useState(0);

  const inputOccupanti = useRef();
  const inputMQ = useRef();

  const Header = ({ title, descrizione }) => (
    <View style={styles.containerTitle}>
      <Text style={styles.title}>{title}</Text>
      {descrizione && <Text style={styles.descrizione}>{descrizione}</Text>}
    </View>
  );
  return (
    <>
      <View
        style={{
          backgroundColor: "#eee",
          padding: 10,
          flexDirection: "row",
          alignItems: "baseline",
        }}
      >
        <Container>
          <Text style={{ fontFamily: Fonts.bold, fontSize: 16 }}>Tari </Text>
          <Text
            style={{
              fontFamily: Fonts.regular,
              fontSize: 12,
              color: "#101010",
            }}
          >
            REPORT TARIFFE
          </Text>
        </Container>
      </View>
      <Container style={{ paddingBottom: 20 }}>
        <Row style={{ paddingTop: 20 }}>
          <Col>
            <ButtonAnni
              selected={selected}
              onSelected={(value) => setSelected(value)}
            />
          </Col>
        </Row>

        <Row>
          <Col style={{ paddingTop: 20 }}>
            <Header title="Quote fisse" />
            <View style={{ flexDirection: "row" }}>
              <RowTari
                selected={selected}
                decimal={5}
                colorPlus="#eee"
                textColorPlus="black"
                borderColorPlus="#ccc"
                viewTariffa="fissa"
              />
            </View>
          </Col>
          <Col style={{ paddingTop: 20 }}>
            <Header title="Differenze anno precedente quote fisse" />
            <View style={{ flexDirection: "row" }}>
              <RowTari
                selected={selected}
                view="differenze"
                viewTariffa="fissa"
                multipler={-1}
              />
            </View>
          </Col>
        </Row>
        <Row>
          <Col style={{ paddingTop: 20 }}>
            <Header title="Quote variabile" />
            <View style={{ flexDirection: "row" }}>
              <RowTari
                selected={selected}
                decimal={5}
                colorPlus="#eee"
                textColorPlus="black"
                borderColorPlus="#ccc"
                viewTariffa="variabile"
              />
            </View>
          </Col>
          <Col style={{ paddingTop: 20 }}>
            <Header title="Differenze anno precedente quote variabili" />
            <View style={{ flexDirection: "row" }}>
              <RowTari
                selected={selected}
                view="differenze"
                viewTariffa="variabile"
                multipler={-1}
              />
            </View>
          </Col>
        </Row>
        <Row>
          <Col style={{ paddingTop: 20 }}>
            <Header title="Spese di notifica" />
            <View style={{ flexDirection: "row" }}>
              <RowTari
                selected={selected}
                view="spesenotifica"
                viewTariffa="fissa"
                colorPlus="#eee"
                textColorPlus="black"
                borderColorPlus="#ccc"
                decimal={2}
              />
            </View>
          </Col>
          <Col style={{ paddingTop: 20 }}>
            <Header title="Differenze anno precedente spese notifica" />
            <View style={{ flexDirection: "row" }}>
              <RowTari
                selected={selected}
                view="speseNotificheDifferenze"
                viewTariffa="fissa"
                multipler={-1}
              />
            </View>
          </Col>
        </Row>
      </Container>
      <View
        style={{
          backgroundColor: "#eee",
          padding: 10,
          flexDirection: "row",
          alignItems: "baseline",
          marginBottom: 20,
        }}
      >
        <Container>
          <Text style={{ fontFamily: Fonts.bold, fontSize: 16 }}>
            Calcolo Tari{" "}
          </Text>
          <Text
            style={{
              fontFamily: Fonts.regular,
              fontSize: 12,
              color: "#101010",
            }}
          >
            Civile Abitazione
          </Text>
        </Container>
      </View>

      <Container style={{ paddingBottom: 20 }}>
        <View style={{ paddingBottom: 20 }}>
          <Text style={{ fontFamily: Fonts.regular, fontSize: 10 }}>
            Il calcolo si riferisce solo alle civili abitazioni, son escluse
            tutte le altre comprese le pertinenze.
          </Text>
        </View>

        <Row>
          <Col>
            <Form>
              <Form.Row>
                <Col>
                  <Form.Control
                    style={{ fontFamily: Fonts.regular, fontSize: 10 }}
                    ref={inputMQ}
                    placeholder="Metri quadrati (MQ)"
                    type="integer"
                  />
                </Col>
                <Col>
                  <Form.Control
                    style={{ fontFamily: Fonts.regular, fontSize: 10 }}
                    ref={inputOccupanti}
                    placeholder="Occupanti"
                    type="integer"
                  />
                </Col>
                <Button
                  style={{ fontFamily: Fonts.regular, fontSize: 10 }}
                  onClick={() => {
                    if (
                      inputOccupanti.current.value > 0 &&
                      inputMQ.current.value > 0
                    ) {
                      setComp(inputOccupanti.current.value);
                      setMq(inputMQ.current.value);
                    }
                  }}
                >
                  Calcola
                </Button>
              </Form.Row>
            </Form>
          </Col>
        </Row>
        {mq > 0 && comp > 0 && (
          <Row>
            <Col style={{ paddingTop: 20 }}>
              <Header title="Tari civili abitazioni" />
              <View style={{ flexDirection: "row" }}>
                <RowTari
                  selected={selected}
                  decimal={2}
                  colorPlus="#eee"
                  textColorPlus="black"
                  borderColorPlus="#ccc"
                  calcolaComponenti={comp}
                  calcolaMQ={mq}
                />
              </View>
            </Col>
            <Col style={{ paddingTop: 20 }}>
              <Header title="Differenze anno precedente Tari civili abitazioni" />
              <View style={{ flexDirection: "row" }}>
                <RowTari
                  selected={selected}
                  decimal={2}
                  view="differenze"
                  multipler={-1}
                  calcolaComponenti={comp}
                  calcolaMQ={mq}
                />
              </View>
            </Col>
          </Row>
        )}
      </Container>
    </>
  );
};
const styles = StyleSheet.create({
  container: {
    //flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    padding: 20,
  },
  header: {},
  descrizione: {
    fontFamily: Fonts.regular,
    fontSize: 12,
  },
  title: {
    fontFamily: Fonts.bold,
    fontSize: 14,
  },
  containerTitle: {
    borderColor: "#ddd",
    borderBottomWidth: 1,
    borderTopWidth: 1,
    padding: 5,
  },
});
