export const TariSpeseNotifiche = [
  { title: 2021, index: "2021", tariffe: [{ tariffaFissa: 2 }] },
  { title: 2020, index: "2020", tariffe: [{ tariffaFissa: 1 }] },
  { title: 2019, index: "2019", tariffe: [{ tariffaFissa: 1 }] },
  { title: 2018, index: "2018", tariffe: [{ tariffaFissa: 1 }] },
  { title: 2017, index: "2017", tariffe: [{ tariffaFissa: 0.5 }] },
];

export const TariSpeseNotificheDifferenze = TariSpeseNotifiche.map(
  (tari, index) => {
    if (index === TariSpeseNotifiche.length - 1) return null;
    let differenza = { ...tari };
    differenza.title =
      differenza.title + "-" + TariSpeseNotifiche[index + 1].title;
    differenza.index = differenza.index + "d";
    differenza.tariffe = tari.tariffe.map((tariffa, i) => {
      let tariffaDifferenza = { ...tariffa };
      tariffaDifferenza.tariffaFissa -=
        TariSpeseNotifiche[index + 1].tariffe[i].tariffaFissa;
      return tariffaDifferenza;
    });
    return differenza;
  }
);

export const TariDifferenze = Tari.map((tari, index) => {
  if (index === Tari.length - 1) return null;
  let differenza = { ...tari };
  differenza.title = differenza.title + "-" + Tari[index + 1].title;
  differenza.index = differenza.index + "d";
  differenza.tariffe = tari.tariffe.map((tariffa, i) => {
    let tariffaDifferenza = { ...tariffa };
    tariffaDifferenza.tariffaFissa -= Tari[index + 1].tariffe[i].tariffaFissa;
    tariffaDifferenza.tariffaVariabile -=
      Tari[index + 1].tariffe[i].tariffaVariabile;
    return tariffaDifferenza;
  });
  return differenza;
});
