import React, { useState } from "react";
import { View, StyleSheet, Text, TouchableOpacity } from "react-native";
import { Fonts } from "../utils/fonts";

export const ButtonAnni = ({ anni, selected, onSelected }) => {
  const Anni = ({ text, value }) => {
    return (
      <TouchableOpacity onPress={() => onSelected(value)}>
        <Text
          style={[
            styles.item,
            {
              backgroundColor: selected === value ? "#007bff" : "white",
              color: selected === value ? "white" : "black",
            },
          ]}
        >
          {text}
        </Text>
      </TouchableOpacity>
    );
  };
  return (
    <View style={styles.container}>
      <Anni text="Ultimi 2 anni" value="2021,2021d,2020" />
      <Anni text="Ultimi 3 anni" value="2021,2021d,2020,2020d,2019" />
      <Anni
        text="Ultimi 4 anni"
        value="2021,2021d,2020,2020d,2019,2019d,2018"
      />
      <Anni
        text="Ultimi 5 anni"
        value="2021,2021d,2020,2020d,2019,2019d,2018,2018d,2017"
      />
    </View>
  );
};
const styles = StyleSheet.create({
  container: { flexDirection: "row" },
  item: {
    padding: 10,
    fontFamily: Fonts.bold,
    fontSize: 12,
    borderRadius: 5,
  },
});
