import React from "react";
import { StyleSheet, Text, View } from "react-native";
import { Fonts } from "../utils/fonts";
import {
  Tari,
  TariDifferenze,
  TariSpeseNotifiche,
  TariSpeseNotificheDifferenze,
} from "../../data/data";
export const RowTari = ({
  view = "tariffe",
  viewTariffa = "fissa",
  decimal = 2,
  multipler = 1,
  colorPlus = "#d4edda",
  colorNeg = "#f8d7da",
  borderColorPlus = "#c3e6cb",
  borderColorNeg = "#f5c6cb",
  textColorPlus = "#155724",
  textColorNeg = "#721c24",
  calcolaMQ = 0,
  calcolaComponenti = 0,
  selected,
}) => {
  const RowTariffe = ({ componente = "", value = 0, title = "" }) => {
    let bgColor = "#eee";
    let textColor = "black";
    let borderColor = "#ccc";
    if (value > 0) {
      bgColor = colorPlus;
      borderColor = borderColorPlus;
      textColor = textColorPlus;
    }
    if (value < 0) {
      bgColor = colorNeg;
      borderColor = borderColorNeg;
      textColor = textColorNeg;
    }
    return (
      <View style={styles.row}>
        {componente != "" && <Text style={styles.cellRow}>{componente}</Text>}
        <View style={styles.cell}>
          {title != "" && <Text style={styles.cellTitle}>{title}</Text>}
          <Text
            style={[
              styles.cellContent,
              {
                color: textColor,
                backgroundColor: bgColor,
                borderColor: borderColor,
              },
            ]}
          >
            {Math.abs(value).toFixed(decimal)}
          </Text>
        </View>
      </View>
    );
  };
  let data = Tari.filter((tari) => selected.includes(tari.index.toString()));

  let dataDiff = null;
  if (view === "differenze") {
    dataDiff = TariDifferenze.filter(
      (tari) => tari != null && selected.includes(tari.index.toString())
    );

    dataDiff[dataDiff.length] = {
      title: "Totali",
      tariffe: dataDiff[0].tariffe.map((r, i) => {
        let val = { ...r };
        val.tariffaFissa = dataDiff.reduce(
          (p, v) => p + v.tariffe[i].tariffaFissa,
          0
        );
        val.tariffaVariabile = dataDiff.reduce(
          (p, v) => p + v.tariffe[i].tariffaVariabile,
          0
        );
        return val;
      }),
    };
  }
  let dataSpese = null;
  if (view === "speseNotificheDifferenze") {
    dataSpese = TariSpeseNotificheDifferenze.filter(
      (spese) => spese != null && selected.includes(spese.index.toString())
    );
    dataSpese[dataSpese.length] = {
      title: "Totali",
      tariffe: dataSpese[0].tariffe.map((r, i) => {
        let val = { ...r };
        val.tariffaFissa = dataSpese.reduce(
          (p, v) => p + v.tariffe[i].tariffaFissa,
          0
        );
        return val;
      }),
    };
  }

  //data.sort((a, b) => a.anno - b.anno);
  //TariDifferenze.sort((a, b) => a.anno - b.anno);
  const culomComponente = (index, componente) => {
    let val =
      componente === 6 ? "(" + componente + "+)" : "(" + componente + ")";
    val += componente === 1 ? " Occupante" : " Occupanti";

    if (!index && view === "spesenotifica") return " ";
    if (!index && view === "speseNotificheDifferenze") return " ";
    if (!index && view != "spesenotifica") return val;
  };
  return data.map((tari, indexAnno) => {
    if (view === "differenze") {
      tari = dataDiff[indexAnno];
    }
    if (view === "spesenotifica") {
      tari = TariSpeseNotifiche[indexAnno];
    }
    if (view === "speseNotificheDifferenze") {
      tari = dataSpese[indexAnno];
    }
    if (tari === null) return null;
    return (
      <View key={indexAnno}>
        {tari.tariffe.map((tariffa, indexTariffa) => {
          let title = indexTariffa === 0 ? tari.title : "";
          let value =
            viewTariffa === "fissa"
              ? tariffa.tariffaFissa
              : tariffa.tariffaVariabile;
          if (calcolaComponenti > 0 && calcolaMQ > 0) {
            const c = calcolaComponenti >= 6 ? 6 : calcolaComponenti;
            if (tariffa.componente != c) return null;
            title = tari.title;
            value = tariffa.tariffaFissa * calcolaMQ + tariffa.tariffaVariabile;
          }
          return (
            <RowTariffe
              key={indexTariffa}
              value={value * multipler}
              componente={culomComponente(indexAnno, tariffa.componente)}
              title={title}
            />
          );
        })}
      </View>
    );
  });
};
const styles = StyleSheet.create({
  fontBold: {
    fontFamily: Fonts.bold,
    fontSize: 30,
  },
  cellContent: {
    fontFamily: Fonts.regular,
    fontSize: 10,
    borderRadius: 5,
    textAlign: "center",
    borderWidth: 1,
  },
  cellRow: {
    fontFamily: Fonts.regular,
    fontSize: 10,
    width: 70,
  },
  row: { flexDirection: "row", alignItems: "flex-end", paddingVertical: 2 },
  cell: { width: 60, paddingHorizontal: 2 },
  cellTitle: {
    textAlign: "center",
    fontFamily: Fonts.bold,
    fontSize: 10,
    paddingVertical: 5,
  },
});
